import Logo from './Images/Logo.svg'
import React, {useState} from "react";

function Arrow() {
	window.onscroll = function() {myFunction()};
	const [Scroll, setScroll] = useState(25);
	function myFunction() {
		setScroll(document.body.scrollTop || document.documentElement.scrollTop)
		console.log(Scroll)
	}
	return (
			<p id="arr" className="fixed w-screen text-center top-3/4">
				<a href="#Body"><i className={Scroll <= 50 ? "arr" : ""}></i></a>
			</p>
	)
}

function Head() {
	return (
		<div id="Head" className="flex-row h-screen bg-black">
			<div className="flex h-screen">
					<img src={Logo} alt="Logo" className="self-center mx-20"/>
					<div className="flex-col self-center">
						<h1 className="text-white text-9xl font-mononoki">
							AbhinavKDev
						</h1>
						<p className="text-3xl text-center text-green font-mononoki">
							GameDev, Programmer, Student, Youtuber (Maybe)
						</p>
					</div>
			</div>
			<Arrow/>
		</div>
	)
}

export default Head;
